/* ============================================
   Card Component - Luxury & Elegant Design
   ============================================ */

/* Global Reset */
* {
  box-sizing: border-box;
}

/* ============================================
   Card Container - Enhanced Luxury Design
   ============================================ */
.card {
  position: relative;
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  padding: 4rem clamp(2.5rem, 6vw, 5rem);
  background: linear-gradient(135deg, 
    rgba(255, 255, 255, 1),
    rgba(250, 251, 252, 0.98)
  );
  border-radius: 32px;
  overflow: hidden;
  box-shadow: var(--shadow-card, 0 6px 20px rgba(0, 0, 0, 0.05), 0 16px 40px rgba(0, 0, 0, 0.07), 0 32px 80px rgba(102, 126, 234, 0.06));
  border: 1px solid rgba(220, 227, 234, 0.4);
  animation: fade-in-card 0.9s cubic-bezier(0.4, 0, 0.2, 1) forwards;
  transition:
    transform 0.5s cubic-bezier(0.4, 0, 0.2, 1),
    box-shadow 0.5s cubic-bezier(0.4, 0, 0.2, 1),
    border-color 0.4s ease,
    background 0.4s ease;
  outline: none;
  font-family: var(--font-main, 'Inter', system-ui, sans-serif);
  will-change: transform, box-shadow;
  backdrop-filter: blur(20px) saturate(180%);
  -webkit-backdrop-filter: blur(20px) saturate(180%);
}

.card::before {
  content: '';
  position: absolute;
  inset: 0;
  background: var(--bg-gradient-sublime, linear-gradient(135deg, rgba(102, 126, 234, 0.05) 0%, rgba(118, 75, 162, 0.05) 100%));
  opacity: 0;
  transition: opacity 0.5s ease;
  pointer-events: none;
  z-index: 0;
}

.card:hover::before {
  opacity: 1;
}

/* Card Variants */
.card-variant-default {
  /* Default styling already applied */
}

.card-variant-elevated {
  box-shadow:
    0 8px 24px rgba(0, 0, 0, 0.06),
    0 16px 48px rgba(0, 0, 0, 0.08),
    0 32px 80px rgba(120, 162, 240, 0.1);
  border: 1px solid rgba(120, 162, 240, 0.2);
}

.card-variant-outlined {
  background: transparent;
  border: 2px solid rgba(220, 227, 234, 0.6);
  box-shadow: none;
}

.card-variant-outlined:hover {
  border-color: var(--accent, #667eea);
  box-shadow: 0 8px 24px rgba(120, 162, 240, 0.15);
}

.card-variant-minimal {
  background: transparent;
  border: none;
  box-shadow: none;
  padding: 2rem clamp(1.5rem, 4vw, 3rem);
}

/* Hover & Focus States - Enhanced */
.card:hover,
.card:focus-within {
  transform: translateY(-8px) scale(1.005);
  box-shadow: var(--shadow-card-hover, 0 16px 40px rgba(0, 0, 0, 0.1), 0 32px 80px rgba(0, 0, 0, 0.12), 0 48px 120px rgba(102, 126, 234, 0.18));
  border-color: rgba(102, 126, 234, 0.5);
  background: linear-gradient(135deg,
    rgba(255, 255, 255, 1),
    rgba(250, 251, 252, 1)
  );
}

.card-variant-elevated:hover {
  box-shadow:
    0 16px 48px rgba(0, 0, 0, 0.1),
    0 32px 80px rgba(0, 0, 0, 0.12),
    0 48px 120px rgba(120, 162, 240, 0.2);
}

.card-variant-minimal:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.06);
}

/* ============================================
   Card Header & Title
   ============================================ */
.card-header {
  position: relative;
  margin-bottom: 2rem;
  text-align: center;
}

.card-title {
  position: relative;
  display: inline-block;
  margin: 0;
  color: var(--text-heading, #1f2d3d);
  font-size: clamp(1.875rem, 4vw, 2.75rem);
  font-weight: 700;
  line-height: 1.2;
  letter-spacing: -0.02em;
  font-family: var(--font-display, 'Inter', system-ui, sans-serif);
  background: var(--accent-gradient, linear-gradient(135deg, #667eea 0%, #764ba2 50%, #f093fb 100%));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  user-select: none;
  outline: none;
  z-index: 1;
  position: relative;
}

.card:hover .card-title,
.card-title:focus {
  transform: translateY(-2px);
  filter: brightness(1.1);
  outline: none;
}

/* ============================================
   Card Body
   ============================================ */
.card-body {
  position: relative;
  color: var(--text-subtle, #5c6b7a);
  font-size: clamp(1rem, 1.6vw, 1.125rem);
  line-height: 1.75;
  font-family: var(--font-main, 'Inter', system-ui, sans-serif);
  transition: color 0.3s ease;
  z-index: 1;
}

.card:hover .card-body {
  color: var(--text-muted, #3b4a59);
}

/* ============================================
   Card Footer
   ============================================ */
.card-footer {
  position: relative;
  margin-top: 2rem;
  padding-top: 2rem;
  border-top: 1px solid rgba(220, 227, 234, 0.5);
  color: var(--text-subtle, #5c6b7a);
  font-size: clamp(0.9rem, 1.4vw, 1rem);
  transition: border-color 0.3s ease;
}

.card:hover .card-footer {
  border-top-color: rgba(120, 162, 240, 0.3);
}

/* ============================================
   Animations
   ============================================ */
@keyframes fade-in-card {
  0% {
    opacity: 0;
    transform: scale(0.97) translateY(40px);
    filter: blur(8px);
  }

  60% {
    opacity: 1;
    transform: scale(1.01) translateY(0);
    filter: blur(2px);
  }

  100% {
    opacity: 1;
    transform: scale(1) translateY(0);
    filter: blur(0);
  }
}

/* ============================================
   Responsive Design - Large Screens
   ============================================ */
@media screen and (width >= 1400px) {
  .card {
    max-width: 1400px;
    padding: 4.5rem clamp(3rem, 7vw, 6rem);
    border-radius: 36px;
  }

  .card-header {
    margin-bottom: 3rem;
  }

  .card-title {
    font-size: clamp(2.25rem, 4.5vw, 3rem);
  }
}

/* ============================================
   Responsive Design - Tablet
   ============================================ */
@media screen and (width <= 1200px) {
  .card {
    max-width: 95%;
    padding: 3rem clamp(2rem, 4vw, 3.5rem);
  }
}

@media screen and (width <= 992px) {
  .card {
    padding: 3rem clamp(2rem, 4vw, 3rem);
    border-radius: 28px;
  }

  .card-header {
    margin-bottom: 1.75rem;
  }

  .card-title {
    font-size: clamp(1.5rem, 3vw, 2rem);
    letter-spacing: -0.6px;
  }

  .card-body {
    font-size: clamp(0.95rem, 1.5vw, 1.05rem);
    line-height: 1.75;
  }

  .card-footer {
    margin-top: 1.75rem;
    padding-top: 1.75rem;
  }
}

@media screen and (width <= 768px) {
  .card {
    padding: 2.5rem clamp(1.75rem, 3vw, 2.5rem);
    border-radius: 24px;
  }

  .card-header {
    margin-bottom: 1.5rem;
  }

  .card-title {
    font-size: clamp(1.4rem, 2.5vw, 1.75rem);
    letter-spacing: -0.5px;
  }

  .card-body {
    font-size: clamp(0.9rem, 1.4vw, 1rem);
    line-height: 1.7;
  }

  .card-footer {
    margin-top: 1.5rem;
    padding-top: 1.5rem;
    font-size: clamp(0.85rem, 1.3vw, 0.95rem);
  }

  .card-variant-minimal {
    padding: 1.75rem clamp(1.25rem, 2.5vw, 1.75rem);
  }
}

/* ============================================
   Responsive Design - Mobile
   ============================================ */
@media screen and (width <= 480px) {
  .card {
    padding: 2rem clamp(1.5rem, 2.5vw, 2rem);
    border-radius: 20px;
  }

  .card-header {
    margin-bottom: 1.25rem;
  }

  .card-title {
    font-size: clamp(1.25rem, 2vw, 1.5rem);
    letter-spacing: -0.4px;
  }

  .card-body {
    font-size: clamp(0.875rem, 1.3vw, 0.95rem);
    line-height: 1.65;
  }

  .card-footer {
    margin-top: 1.25rem;
    padding-top: 1.25rem;
    font-size: clamp(0.8rem, 1.2vw, 0.9rem);
  }

  .card-variant-minimal {
    padding: 1.5rem clamp(1rem, 2vw, 1.5rem);
  }
}

/* ============================================
   Landscape Orientation
   ============================================ */
@media screen and (orientation: landscape) and (width <= 768px) {
  .card {
    padding: 2rem clamp(1.75rem, 3vw, 2.5rem);
    border-radius: 20px;
  }

  .card-title {
    font-size: clamp(1.35rem, 2.2vw, 1.6rem);
  }

  .card-body {
    font-size: clamp(0.9rem, 1.3vw, 1rem);
  }
}

/* ============================================
   Reduced Motion (Accessibility)
   ============================================ */
@media (prefers-reduced-motion: reduce) {
  .card {
    animation: none;
    transition: opacity 0.3s ease;
  }

  .card:hover,
  .card:focus-within {
    transform: none;
  }

  .card:hover .card-title,
  .card-title:focus {
    transform: none;
  }

  .card-variant-minimal:hover {
    transform: none;
  }
}

/* ============================================
   Print Styles
   ============================================ */
@media print {
  .card {
    page-break-inside: avoid;
    box-shadow: none;
    border: 1px solid #e0e0e0;
    padding: 1.5rem;
  }

  .card-title {
    -webkit-text-fill-color: #1f2d3d;
    color: #1f2d3d;
  }

  .card:hover {
    transform: none;
    box-shadow: none;
  }
}

/* ============================================
   High Contrast Mode (Accessibility)
   ============================================ */
@media (prefers-contrast: high) {
  .card {
    border: 2px solid var(--text-heading, #1f2d3d);
  }

  .card-title {
    -webkit-text-fill-color: var(--text-heading, #1f2d3d);
    color: var(--text-heading, #1f2d3d);
  }

  .card-footer {
    border-top-color: var(--text-heading, #1f2d3d);
  }
}
